.search-bar {

    display: grid;

    .search-bar-wrapper {

        display: grid;
        width: 100%;
        grid-row-gap: 23px;

        input {
            padding-left: 35px;
            color: $color-gray-one;
            box-shadow: 2px 3px 25px 1px rgba(0, 0, 0, 0.1);
            border: 0px solid transparent;
            border-radius: 5px;
            transition: all .2s ease-in-out;

            &::placeholder {
                color: $color-gray-two;
                font-family: 'Font Awesome\ 5 Free', 'Lato', Arial, Helvetica, sans-serif;
                font-weight: 600;
            }

            &:focus {
                outline: none;
                box-shadow: 2px 3px 25px 1px rgba(0, 0, 0, 0.3);;
            }
        }

        p {
            text-align: right;
            font-size: 14px;
            color: $color-gray-two;
        }
    }

}

.search-bar-app {
    align-content: start;
    justify-items: center;

    input {
        height: 100px;
        font-size: 40px;
    }
}

.search-bar-results {

    align-content: start;
    justify-items: center;

    input {
        font-size: 22px;
        height: 36px;
    }
}